.container {
  text-align: center;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}

.breed-select {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
}

.loader {
  display: none;
}

.loader:after {
  content: "";
  background: #ff3d00;
  width: 0%;
  height: 4px;
  animation: 5s linear infinite lineGrow;
  display: block;
}

.error {
  color: red;
  display: none;
}

.cat-info {
  text-align: left;
  margin-top: 20px;
  display: none;
}

.cat-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.cat-name {
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-description {
  margin-bottom: 10px;
}

.cat-temperament {
  font-style: italic;
}

.utilit-js {
  display: none;
}

